import React, {useState} from 'react';
import ProfileInfo from "./proifle-info/profileInfo";
import MyPostsContainer from "./myPosts/myPostsContainer";
import {Field} from "redux-form";
import {withInput, withTextArea} from "../inputs/textarea";
import {reduxForm} from "redux-form";

const Content = (props) => {

    const onFormSubmit = (formData) => {
        props.updateProfileInfo(formData)
            .then(() => setEditMode(false))
            .catch((e) => {});
    }

    const [ editMode, setEditMode ] = useState(false);

    return (
        <div className={"main-content"}>
            <button onClick={() => setEditMode(true) }>Edit</button>
            { editMode ?
                <ProfileFormEditRedux
                    userInfo={props.userInfo}
                    initialValues={props.userInfo}
                    onSubmit={ onFormSubmit }
                /> :
                <ProfileInfo { ...props } />
            }
            <MyPostsContainer />
        </div>
    )
}

const ProfileFormEdit = ({ handleSubmit, userInfo, error }) => {
    return (
        <form onSubmit={ handleSubmit }>
            <div>{ error && <div>Something went wrong</div> }</div>
            <button>save</button>
            <div>
                <b>Full name:</b>
                <Field name="fullName" placeholder={"Full name"} component={ withInput } />
            </div>
            <div>
                <b>Looking for a job:</b>
                <Field name="lookingForAJob" type="checkbox" component="input"/>
            </div>
            <div>
                <b>Looking for a job description:</b>
                <Field name="lookingForAJobDescription" component={ withTextArea } />
            </div>
            <div>
                <b>My professional skills:</b>
                <Field name="skills" component={ withTextArea } />
            </div>
            <div>
                <b>About me:</b>
                <Field name="aboutMe" component={ withTextArea } />
            </div>
            <div>
                <b>Contacts:</b>
                { Object.keys(userInfo.contacts).map(key => {
                    return <Field component={ withInput } name={"contacts." + key} placeholder={ key } />
                }) }
            </div>
        </form>
    )
};

const ProfileFormEditRedux = reduxForm(
    { form: "edit-profile" }
)(ProfileFormEdit);

export default Content;