import React from 'react';
import Loader from "../../loader/loader";
import {Redirect} from "react-router-dom";
import ProfileStatus from "./profileStatus";
import userImage from "../../../images/user.png";
import withLoader from "../../../hoc/withLoader";

const ProfileInfo = ({ setUserPhotoThunk, userInfo, isAuth, id, status, setUserStatusThunk, owner, ...props }) => {
    if (!isAuth) {
        return <Redirect to="/login" />
    }

    if (!userInfo) {
        return <Loader />;
    }

    const onInputChanged = ({ target }) => {
        if (target.files.length) {
            setUserPhotoThunk(target.files[0]);
        }
    }

    const {
        contacts, fullName, photos: { large }, aboutMe, lookingForAJob,lookingForAJobDescription
    } = userInfo;
    return (
        <>
            <div className="main-image">
                <img src={ large ||  userImage } alt="main" />
                { owner && <input type="file" onChange={ onInputChanged }/> }
            </div>
            <div>
                <b>Looking for a job:</b> { lookingForAJob ? 'yes' : 'no' }
            </div>
            <div>
                <b>About me:</b> { aboutMe ? aboutMe : 'not added'}
            </div>
            <div>
                { lookingForAJob && <b>My professional skills: { lookingForAJobDescription }</b> }
            </div>
            <div>
                {
                    Object.entries(contacts).map(([ key, value ]) => {
                        return <Contact key={ key } title={ key } value={ value } />;
                    })
                }
            </div>
            <h1>{ fullName }</h1>
            <ProfileStatus status={status} id={id} setUserStatusThunk={setUserStatusThunk}/>
        </>
    );


}

const Contact = ({ title, value }) => <div>{ title }: { value }</div>;

export default ProfileInfo;